import {CONTACTLIST,CONTACTDETAIL} from "../../../utils/api.js"
export default {
	data() {
		return {
			list:[],
			dataList:[],
			listCur: [],
			boxTop: "",
			barTop: "",
			hidden: false,
			offsettop: "",
			barHeight: "",
			listCurID: "A",
			searchInput: '',
			listData: ''
		}
	},
	onShow() {
		// this.initData()
		uni.showToast({
			title:'加载中...',
			icon:'none',
			mask:true
		})
		// 通讯录列表
		this.getAddressBook()
	},
	onLoad() {
		// this.initData()
		// 通讯录列表
		// this.getAddressBook()
	},
	onPullDownRefresh() {
	},
	methods: {
		// 初始化数据
		initData() {
			// for (let i = 0; i < 26; i++) {
			// 	this.list[i] = String.fromCharCode(65 + i)
			// }
			// this.listCur = this.list[0]
			var initList = []
			for (let i =0;i<26;i++) {
				this.list.push({letter:String.fromCharCode(65 + i),data:[]})
			}
		},
		// 通讯录列表
		getAddressBook(){
			const params = {}
			var upperCase;
			this.list = []
			this.initData()
			this.$request.url_request(CONTACTLIST,params,"GET",res =>{
				uni.stopPullDownRefresh()
				 this.listData = JSON.parse(res.data).data
				for(let i=0;i<this.listData.length;i++){
					upperCase = this.listData[i].pinyin.substring(0,1).toUpperCase()
					for(let j=0;j<=25;j++){
						if(upperCase == this.list[j].letter){
							this.list[j].data.push(this.listData[i])
						}
					}
				}
					uni.hideToast()
			},err =>{})
		},
		// 搜索
		search(){
			console.log(this.searchInput)
			console.log(this.listData)
		},
		// 获取文字信息
		getCur(e) {
			this.hidden = true
			this.listCur = this.list[e].letter
			this.listCurID = this.list[e].letter
		},
		setCur(e) {
			this.hidden = false
			this.listCur = this.list[e].letter
		},
		// 滑动选择item
		tMove(e) {
			this.hidden = true
			console.log(e.detail.id)
		},
		// 触发全部开始选择
		tStart() {
			this.hidden = true
		},
		//触发结束选择
		tEnd() {
		    this.hidden = false,
		    this.listCurID = this.listCur
		},
		// 添加联系人
		addContact() {
			uni.navigateTo({
				url: "../AddContact/AddContact"
			})
		},
		// 通讯录详情
		contactDetail(tel,head) {
			const detail = JSON.stringify({mobile:tel,head:head})
			uni.navigateTo({
				url: "../ContactDetail/ContactDetail?detail="+detail
			})
		}
	}
}
